import React, { useState, useEffect } from 'react';
import {
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  TextField,
  Button,
  Box,
  Typography,
} from '@mui/material';
import { axiosClient } from '../../utils/axiosClient';
import dayjs from 'dayjs';
import { DatePicker, LocalizationProvider } from '@mui/x-date-pickers';
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs';
import { DemoContainer } from '@mui/x-date-pickers/internals/demo';
import { useMember } from '../../contexts/MemberContext';

const NickInputModal = () => {
  const [showModal, setShowModal] = useState(false);
  const [completionModal, setCompletionModal] = useState(false); //    
  const [nick, setNickname] = useState('');
  const [birth, setBirthdate] = useState(dayjs());
  const [isNicknameAvailable, setIsNicknameAvailable] = useState(null); // null 
  const { setMeId, setNick, setName } = useMember(); // MemberContext setter  


  const handleModalSubmit = async () => {
    try {
      await axiosClient.post('/member/update', {
        nick,
        birth: birth.format('YYYY-MM-DD'),
      });
      setShowModal(false);
      setNickname('');
      setBirthdate(dayjs());
      setCompletionModal(true); //      
  
      //      
      const memberData = await axiosClient.get('/member/get_me');
      const { id, nick: fetchedNick, name } = memberData.data; //   
  
      setMeId(id);
      setNick(fetchedNick);
      setName(name);
    } catch (error) {
      console.error('Error submitting data:', error);
    }
  };
  

  const checkNicknameAvailability = async () => {
    try {
      const response = await axiosClient.get(`/member/nick?nick=${nick}`);
      setIsNicknameAvailable(!response.data);
    } catch (error) {
      console.error('Error checking nickname:', error);
    }
  };

  useEffect(() => {
    axiosClient.setShowModalHandler(setShowModal);
  }, []);

  return (
    <div>
      <Dialog open={showModal}>
        <DialogTitle> !</DialogTitle>
        <DialogContent>
          <Typography variant="body2" color="textSecondary" gutterBottom>
              .
          </Typography>
          <Box display="flex" alignItems="center" marginBottom={2}>
            <TextField
              autoFocus
              margin="dense"
              label=""
              type="text"
              fullWidth
              variant="outlined"
              value={nick}
              onChange={(e) => {
                setNickname(e.target.value);
                setIsNicknameAvailable(null); //     
              }}
              error={isNicknameAvailable === false} //     
              helperText={
                isNicknameAvailable === false
                  ? '   .'
                  : isNicknameAvailable === true
                  ? '  .'
                  : ''
              }
            />
            <Button
              onClick={checkNicknameAvailability}
              variant="outlined"
              size="medium"
              style={{ marginLeft: '8px' }}
            >
              
            </Button>
          </Box>

          <LocalizationProvider dateAdapter={AdapterDayjs}>
            <DemoContainer components={['DatePicker']}>
              <DatePicker
                label=""
                value={birth}
                onChange={(newValue) => setBirthdate(newValue)}
                views={['year', 'month', 'day']}
              />
            </DemoContainer>
          </LocalizationProvider>
        </DialogContent>
        <DialogActions>
          <Button
            onClick={handleModalSubmit}
            disabled={isNicknameAvailable !== true} //    
          >
            
          </Button>
        </DialogActions>
      </Dialog>

      {/*   */}
      <Dialog open={completionModal} onClose={() => setCompletionModal(false)}>
        <DialogTitle>!</DialogTitle>
        <DialogContent>
          <Typography>   .</Typography>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setCompletionModal(false)}></Button>
        </DialogActions>
      </Dialog>
    </div>
  );
};

export default NickInputModal;
