import { AddCircleOutline } from "@mui/icons-material";
import { Avatar, Box, IconButton, List, ListItem, Modal, Tooltip, Typography, Button, TextField } from "@mui/material";
import { grey } from "@mui/material/colors";
import { useEffect, useState } from "react";
import { axiosClient } from "../../utils/axiosClient";
import { useNavigate } from 'react-router-dom'; // useNavigate 
import { useGroup } from "../../contexts/GroupContext";
import { useGroupNotification } from "../../contexts/GroupNotificationContext";

function GroupSideBar() {
    const [groupList, setGroupList] = useState([]);
    const [openModal, setOpenModal] = useState(false);
    const [newGroupName, setNewGroupName] = useState("");
    const navigate = useNavigate(); 
    const { setGroupId, setGroupName, setTotal, setRtype, setMembers } = useGroup(); 
    const {invitationCount } = useGroupNotification();

    //    
    const fetchGroups = async () => {
        try {
            const res = await axiosClient.get("/room/list/group");
            setGroupList(res.data); //    state 
        } catch (error) {
            console.error("Error fetching group data:", error);
            setGroupList([]); //      
        }
    };

    useEffect(() => {
        fetchGroups(); //       
        console.log(""+ invitationCount)
    }, [invitationCount]);

    //    / 
    const handleOpenModal = () => setOpenModal(true);
    const handleCloseModal = () => setOpenModal(false);

    //    
    const handleCreateGroup = async () => {
        try {
            const res = await axiosClient.post("/room/create", { groupName: newGroupName });
            fetchGroups(); //        
            setNewGroupName(""); //   
            handleCloseModal(); //  
        } catch (error) {
            console.error("Error creating group:", error);
        }
    };

    //  avatar  
    const handleGroupClick = async(groupId) => {
        const res= await axiosClient.get(`/room/detail/${groupId}`)
        console.log(res.data)
        setGroupId(res.data.id)
        setGroupName(res.data.groupName)
        setTotal(res.data.total)
        setRtype(res.data.rtype)
        setMembers(res.data.members)
        navigate(`/chat`); //  ID  ChatRoom 
    };

    return (
        <Box sx={{ width: 100, bgcolor: '#697565', p: 2 , height: '100vh', display: 'flex', flexDirection: 'column'}}>
            <Typography variant="h6" color={"white"} noWrap>
                Room
            </Typography>
            <List sx={{ flex: 1, overflowY: 'auto', scrollbarWidth: 'none', '&::-webkit-scrollbar': { display: 'none' } }}>
                {Array.isArray(groupList) && groupList.length > 0 ? (
                    groupList.map((group) => (
                        <ListItem
                            key={group.id} //  ID 
                            button
                            sx={{
                                display: 'flex',
                                flexDirection: 'column',
                                alignItems: 'center',
                                justifyContent: 'center',
                                '&:hover': {
                                    bgcolor: '#e0e0e0', //    
                                },
                            }}
                            onClick={() => handleGroupClick(group.id)} //    ID 
                        >
                            <Tooltip title={group.groupName} arrow>
                                <Avatar sx={{ bgcolor: grey[500] }}>{group.groupName[0]}</Avatar>
                            </Tooltip>
                            <Typography variant="caption" color={"white"} noWrap>
                                {group.groupName}
                            </Typography>
                        </ListItem>
                    ))
                ) : (
                    <Typography variant="caption" color={"white"}>
                        <br/>
                        !
                    </Typography>
                )}
                <ListItem
                    button
                    sx={{ justifyContent: 'center', mt: 'auto', display: 'flex', flexDirection: 'column' }}
                    onClick={handleOpenModal} //     
                >
                    <Tooltip title={" "} arrow>
                        <IconButton>
                            <AddCircleOutline sx={{ fontSize: 40 }} />
                        </IconButton>
                    </Tooltip>
                </ListItem>
            </List>

            {/*    */}
            <Modal open={openModal} onClose={handleCloseModal}>
                <Box sx={{
                    position: 'absolute',
                    top: '50%',
                    left: '50%',
                    transform: 'translate(-50%, -50%)',
                    width: 400,
                    bgcolor: 'background.paper',
                    borderRadius: 2,
                    boxShadow: 24,
                    p: 4,
                }}>
                    <Typography variant="h6" mb={2}>
                         
                    </Typography>
                    <TextField
                        fullWidth
                        label=" "
                        value={newGroupName}
                        onChange={(e) => setNewGroupName(e.target.value)}
                        variant="outlined"
                        mb={2}
                    />
                    <Box sx={{ display: 'flex', justifyContent: 'flex-end', mt: 2 }}>
                        <Button variant="contained" onClick={handleCreateGroup} disabled={!newGroupName}>
                            
                        </Button>
                        <Button onClick={handleCloseModal} sx={{ ml: 2 }}>
                            
                        </Button>
                    </Box>
                </Box>
            </Modal>
        </Box>
    );
}

export default GroupSideBar;
